document.addEventListener("DOMContentLoaded", init);

function init() {
    const masterTL = gsap.timeline()

    masterTL
        .set('.heart-small', {scale: 0})
        .set('.letter', {scale: 0})
        .from('#heart', {scale: 0, duration: 2.5, ease: "bounce"})
        .from("#letter-arrow", {x: -500, y: 250, duration: 0.5})
        .to('#heart', {scale: 0, duration: 1.5, ease: "bounce"}, "-=0.5")
        .to(".heart-small", {
            scale: 1,
            x: "random(-500, 500)",
            y: "random(-500, 500)",
            ease: "power1.inOut",
            duration: 2.5,
            stagger: {
                from: "random",
                axis: "x",
                ease: "power2.in",
                amount: 1.5
            },
            repeat: -1,
            repeatRefresh: true
        }, "<")
        .from("#letter-i", {scale: 0, duration: 1, transformOrigin: "50% 50%"}, '<')
        .from("#letter-t", {scale: 0, duration: 1, transformOrigin: "50% 50%"}, "-=0.5")
        .from("#letter-lines", {scale: 0, y: 500, duration: 1, transformOrigin: "50% 50%"}, "-=0.5")
        .from("#letter-two", {scale: 0, duration: 1, transformOrigin: "50% 50%"}, "-=0.5")
        .from("#letter-g", {scale: 0, duration: 1, transformOrigin: "50% 50%"}, "-=0.5")
        .to('#heart', {scale: 1.1, duration: 2.5, ease: "bounce"})

    const repeat = document.querySelector('#repeat')

    repeat.onclick = function (e) {
        masterTL.restart();
    }

}
